body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.invisible{
    display:none;
}

.banner-msg{
    width:500px;
    font-size: 30px;
    background-color: #E9967A;
    border-radius: 12px;
    text-align: center;
}

#upload-photo{
    margin-top: 30px; 
    margin-left:30px; 
}



.col{
    margin: 3%;
}

.card{
    height: 333px;
    width: 250px;
    box-shadow: 5px 10px 8px;
}

.card-img{
    height: 70%;
    width: 90%;
}

.card-text{
    color: #A9A9A9;
}

.not-found-msg{
    margin-left: 300px; 
}



.form-element{
    margin:5px; 
}

#search-photo{
    margin-top: 30px; 
    margin-left:30px; 
}

#header{
    padding: 10px;
    background-color:hsl(180, 50%, 50%);
    text-align: center;
    padding-bottom:1px; 
}

#title{
    color:hsl(227, 100%, 13%);
}



.form-element{
  margin:5px;    
}

.text-input{
  width: 450px;
}

.all-links{
  box-shadow: 0px 6px 8px;
  text-align: center;
  background-color:#D3D3D3;
}

.link{
  margin: 30px;
  color:#191970;
  opacity: 0.5;
}

.link:hover{
  text-decoration: none;
}

.link:focus{
  opacity: 1;
}



